<template>
    <div class="app">
        <div class="titleH1">
            <div class="textH1"> Những công việc cần làm </div>
        </div>
        <div class="head">
            <div class="Addnew">
                <div class="form-add">
                    <div style="margin-left: 20px;">
                        <b-button v-b-toggle.sidebar-footer>
                            <b-icon icon="list"></b-icon>
                        </b-button>
                        <b-sidebar id="sidebar-footer" backdrop-variant="dark" backdrop
                            aria-label="Sidebar with custom footer" no-header shadow>
                            <template #footer="{ hide }">
                                <div class="d-flex bg-dark text-light align-items-center px-3 py-2">
                                    <strong class="mr-auto"></strong>
                                    <b-button size="sm" @click="hide">Close</b-button>
                                </div>
                            </template>
                            <div class="px-3 py-2">
                                <div>
                                    <b-list-group style="max-width: 300px;">
                                        <b-list-group-item v-for="item, index in listMusic" :key="index"
                                            style="text-align: center;">
                                            <b-embed type="iframe" aspect="4by3" :src="item.link" allowfullscreen allow='autoplay'></b-embed>
                                            <b-icon icon="x-circle"
                                                style="cursor: pointer; width: 20px;height: 20px; margin-top: 10px;"
                                                @click="deleteVideo(index)"></b-icon>
                                        </b-list-group-item>
                                    </b-list-group>
                                    <div>
                                        <div style="text-align: center; margin-top: 20px; cursor: pointer;">
                                            <b-icon icon="plus-lg" v-b-modal.modal-prevent-closing
                                                style="width: 25px; height: 25px;"></b-icon>
                                        </div>
                                        <b-modal id="modal-prevent-closing" ref="modal" title="Điển link video vào đây"
                                            @show="resetModal" @hidden="resetModal" @ok="handleOk">
                                            <b-input v-model="music"></b-input>
                                        </b-modal>
                                    </div>

                                </div>
                            </div>
                        </b-sidebar>
                    </div>

                    <div style="display: flex; flex: 1; justify-content: center;">
                        <b-input type="text" v-model="value" placeholder="Nội dung cần thêm mới" class="input"
                            style="border-radius: 10px;" @keypress="handleEnter" />

                        <b-button @click="handleClick"
                            style="margin-left: 10px;border: none; border-radius: 10px;">Add</b-button>

                        <b-form-datepicker v-model="valueDate" class="datepicker"/>
                    </div>
                </div>
            </div>
        </div>
        <div style="flex: 1; overflow: auto; padding-bottom: 70px;padding-top: 11px;"
            :style="`background-image: url(${VUE_APP_URL_IMAGE}aaa.jpg); background-size: contain; background-repeat: round;`">
            <div style="width: 100%;height: 100%;align-items: center; display: flex;flex-direction: column; overflow: auto;">
                <div v-for="(item, index) in list" :key="index" style="width: 100%; padding: 0 20px;">
                    <div :id="`to-do${index}`" class="to-do animate__animated animate__fadeIn">
                        <div>
                            <b-checkbox class="custom-checkbox" v-model="item.checked"
                                @change="handleChange($event, item)"></b-checkbox>
                        </div>
                        <div style="background-color: white; color: black; border: 5px solid pink; width: 800px;justify-content: center; align-items: center; display: flex; border-radius: 10px;"
                            :style="item.checked ? 'text-decoration-line: line-through;' : ''">
                            <p style="word-break: break-word; margin: 5px;">
                                {{ item.text }}
                            </p>
                        </div>
                        <div style="color: red; font-size: 30px; cursor: pointer" @click="close(index)">
                            &#x2715;
                        </div>
                        <div style="color: white; font-size: 30px; cursor: pointer" @click="edit(item, index)">
                            &#9997;
                        </div>
                    </div>
                </div>
                <div class="delete-all">
                <b-button variant="danger" @click="deleteAll">
                    <i class="fa fa-trash-o"></i>
                </b-button>
            </div>
            </div>
          
        </div>

        <transition name="fade" appear>
            <div class="modal-overlay" v-if="open" @click="closeModal"></div>
        </transition>
        <transition name="pop" appear>
            <div class="modal-edit" role="dialog" v-if="open">
                <div class="modal-content-edit">
                    <div class="title-edit">
                        <h3> Edit something </h3>
                        <span class="close" @click="closeModal">&times;</span>
                    </div>
                    <div class="content-edit">
                        <input type="text" v-model="valueEdit" style="width: 100%; height: 44px;" @keyup.enter="confirm">
                    </div>
                    <button @click="confirm"
                        style="margin-top: 20px; width: 100px; height: 50px; font-size: 15px; background-color: pink; border: 3px solid black; border-radius: 10px;">
                        Confirm </button>
                </div>
            </div>
        </transition>
    </div>
</template>
  
<script>
export default {

    name: 'Test',

    data() {
        return {
            nameState: null,
            open: false,
            value: "",
            indexEdit: "",
            valueEdit: "",
            valueDate: new Date().toISOString().substring(0, 10),
            music: "",
            VUE_APP_URL_IMAGE: process.env.VUE_APP_URL_IMAGE,
            list: [],
            listMusic: [
                {
                    link: "https://www.youtube.com/embed/wCx-Kf5GUxE"
                },
            ]
        }
    },

    created() {
        if (JSON.parse(localStorage.getItem(`todoList-${this.valueDate}`))) {
            this.list = JSON.parse(localStorage.getItem(`todoList-${this.valueDate}`));
        }
        if (JSON.parse(localStorage.getItem("musicList"))) {
            this.listMusic = JSON.parse(localStorage.getItem("musicList"))
        }
    },

    watch: {
        list: {
            handler() {
                localStorage.setItem(`todoList-${this.valueDate}`, JSON.stringify(this.list));
            }
        },
        listMusic: {
            handler() {
                localStorage.setItem("musicList", JSON.stringify(this.listMusic));
            }
        },
        valueDate: {
            handler() {
                if (JSON.parse(localStorage.getItem(`todoList-${this.valueDate}`))) {
                    this.list = JSON.parse(localStorage.getItem(`todoList-${this.valueDate}`));
                } 
                else {
                    this.list = [];
                }
            }
        }
    },

    methods: {
        handleClick() {
            if (this.value) {
                this.list.push({
                    checked: false,
                    text: this.value,
                });
                this.value = ""
            }
        },

        handleEnter(event) {
            if (event.keyCode === 13 || event.key === 'Enter') {
                this.handleClick();
            }
        },
        close(index) {
            const temp = document.getElementById(`to-do${index}`);

            temp.className += " animate__fadeOut";

            setTimeout(() => {
                this.list.splice(index, 1);

                temp.className = temp.className.replace(" animate__fadeOut", "");
            }, 500)
        },
        deleteAll() {
            this.list = []
        },
        handleChange(event, item) {
            // console.log(event);
        },
        edit(item, index) {
            this.open = true;
            this.valueEdit = item.text;
            this.indexEdit = index;
        },
        closeModal() {
            this.open = false;
        },
        confirm() {
            this.list[this.indexEdit].text;
            this.list[this.indexEdit].text = this.valueEdit;
            this.closeModal();
        },

        resetModal() {
            this.name = ''
            this.nameState = null
        },

        handleOk(event) {
            event.preventDefault();
            this.handleSubmit();
        },

        handleSubmit() {
            if (!this.music) {
                return alert("Vui lòng nhập link nhạc");
            }

            this.listMusic.push({
                link: this.music.replace("youtu.be", "youtube.com/embed").replace("watch?v=", "embed/")
            });
            this.music = ""

            this.$nextTick(() => {
                this.$refs['modal'].hide()
            });
        },
        deleteVideo(index) {
            this.listMusic.splice(index, 1);
        }
    }

}


</script>
  
<style>
.app {
    width: 100%;
    height: calc(100vh);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    font-family: 'Dancing Script', cursive;
}

.titleH1 {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: pink;
    font-size: 60px;
    color: #32C0B0;
    font-weight: 500;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.datepicker {
    margin-left: 5px;
    width: 250px !important;
    height: 50px !important;
}

.datepicker label {
    display: flex;
    align-items: center;
}
.to-do {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
}

.delete-all {
    display: flex;
    justify-content: flex-end;
    right: 100px;
    position: absolute;
    bottom: 10px;
}

.delete-all button {
    border-radius: 20px;
    border: none;
}

.delete-all button i {
    font-size: 60px;
}

@media only screen and (max-width: 1000px) {
    .to-do {
        width: 100%;
    }

    .delete-all {
        right: 3%;
        bottom: 15px;
    }

    .delete-all button i {
        font-size: 30px;
    }

    .titleH1 {
        font-size: 35px;
    }

    .datepicker label {
        display: none;
    }

    .datepicker {
        width: fit-content !important;
    }
    .datepicker .dropdown-menu {
    top: 12px !important;
    left: -120px !important;
}

}

.head {
    background-color: pink;
    height: 70px;
    display: flex;
    justify-content: space-between;
    justify-items: center;
    align-items: center;
}

.Addnew {
    height: 100%;
    width: 100%;
}

.Addnew .form-add {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.Addnew .form-add input {
    width: 50%;
    height: 50px;
    font-family: 'Dancing Script', cursive;
    padding-left: 10px;
    font-size: 20px;
    outline: none;
    border: none;
}

.Addnew .form-add button {
    height: 50px;
    cursor: pointer;
    font-family: 'Dancing Script', cursive;
    font-size: 20px;
    transition: all .3s;
    background-color: #32C0B0
}

.Addnew .form-add button:hover {
    background-color: #6ee9db;
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
.modal-edit {
    position: absolute;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    overflow: auto;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    text-align: center;
    width: fit-content;
    height: fit-content;
    padding: 2rem;
    border-radius: 1rem;
    z-index: 999;
    transform: none;
}

.modal-overlay {
    content: '';
    position: absolute;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 998;
    background: #2c3e50;
    opacity: 0.6;
    cursor: pointer;
}

.modal-content-edit {
    background-color: #fefefe;
    padding: 20px;
    border: 5px solid #888;
    border-radius: 50px;
    width: 500px;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.title-edit {
    display: flex;
    justify-content: space-between;
    justify-items: center;
    align-items: center;
}

.content-edit {
    margin-top: 10px;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity .4s linear;
}

.fade-enter,
.fade-leave-to {
    opacity: 0;
}

.pop-enter-active,
.pop-leave-active {
    transition: transform 0.4s cubic-bezier(0.5, 0, 0.5, 1), opacity 0.4s linear;
}

.pop-enter,
.pop-leave-to {
    opacity: 0;
    transform: scale(0.3) translateY(-50%);
}

.custom-checkbox {
    margin-right: 10px;
    margin-bottom: 15px;
}

.custom-checkbox .custom-control-label::before {
    height: 30px !important;
    width: 30px !important;
}

.custom-checkbox .custom-control-label::after {
    height: 30px !important;
    width: 30px !important;
}</style>


  